html{font-size:18px;position:relative;height:100vh;font-family:'Inconsolata', monospace;background-color:#131313;color:rgba(255,250,245,0.85);-webkit-user-select:none;-ms-user-select:none;user-select:none}html::before{content:'</>';position:absolute;color:rgba(255,255,255,0.295);top:10px;left:20px;font-size:100px}.hide{display:none !important}body,#root{margin:0px;height:100vh;overflow:hidden}.project{text-decoration:none;width:100%;box-sizing:border-box;display:flex;margin:20px 0px;border-radius:5px;padding:5px;background-color:transparent;-webkit-animation:FadeIn 0.6s ease-out;animation:FadeIn 0.6s ease-out}.project *{color:rgba(255,250,245,0.85)}.project img{width:250px;border-radius:5px;object-fit:contain;background-color:black}.project .project-title{width:100%;font-size:20px;margin:0px;padding:0px}.project .project-info{display:flex;flex-direction:column;padding:0px 0px 10px 30px;line-height:1.2rem}.project .project-info .project-des{font-size:16px;margin:0px;margin-top:10px;padding:0px}.project:hover{cursor:pointer;background-color:rgba(58,58,58,0.048)}.project:hover *{color:#fffaf5}@-webkit-keyframes TitleFadeIn{0%{-webkit-transform:scaleX(0);transform:scaleX(0);opacity:0}30%{-webkit-transform:scaleX(0.95);transform:scaleX(0.95);opacity:0}100%{-webkit-transform:scaleX(1);transform:scaleX(1);opacity:1}}@keyframes TitleFadeIn{0%{-webkit-transform:scaleX(0);transform:scaleX(0);opacity:0}30%{-webkit-transform:scaleX(0.95);transform:scaleX(0.95);opacity:0}100%{-webkit-transform:scaleX(1);transform:scaleX(1);opacity:1}}.introduce{-webkit-animation:TitleFadeIn 0.5s ease-in-out;animation:TitleFadeIn 0.5s ease-in-out}.mainContent{-webkit-animation:FadeIn 1s ease-in;animation:FadeIn 1s ease-in;width:100%;max-width:500px}.sendMeEmail{-webkit-animation:FadeIn 0.6s ease-out;animation:FadeIn 0.6s ease-out;position:relative;display:flex;flex-direction:column;width:100%}.sendMeEmail *{box-sizing:border-box;font-size:14px;border:none;border-radius:5px;padding:10px;margin:5px 0px;width:100%;resize:none}.sendMeEmail textarea{height:150px}.sendMeEmail button{font-family:'Inconsolata', monospace;font-weight:800;font-size:16px;background-color:#39da6e;width:60px;position:absolute;bottom:8px;right:8px}.sendMeEmail button:hover{cursor:pointer}@-webkit-keyframes FadeIn{0%{opacity:0;-webkit-transform:translateY(30px);transform:translateY(30px)}60%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}80%{opacity:1;-webkit-transform:translateY(5px);transform:translateY(5px)}100%{opacity:1;-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes FadeIn{0%{opacity:0;-webkit-transform:translateY(30px);transform:translateY(30px)}60%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}80%{opacity:1;-webkit-transform:translateY(5px);transform:translateY(5px)}100%{opacity:1;-webkit-transform:translateY(0px);transform:translateY(0px)}}@-webkit-keyframes ZoomIn{0%{-webkit-transform:scale(3);transform:scale(3);opacity:0}20%{-webkit-transform:scale(2);transform:scale(2);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}@keyframes ZoomIn{0%{-webkit-transform:scale(3);transform:scale(3);opacity:0}20%{-webkit-transform:scale(2);transform:scale(2);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}.highlight{color:#fffaf5}p{color:rgba(255,250,245,0.85);margin:10px;padding:0px}.greed{margin-bottom:10px;font-size:1.75rem}.skillBar,.navBar{display:flex;flex-wrap:wrap;align-items:center;justify-content:center}.skillBar *,.navBar *{margin:10px 0px}main{width:90%;margin:auto;padding:20px;position:relative;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);transition:0.25s ease-in-out;font-family:'Inconsolata', monospace;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:20px;gap:20px}.splide-holder{-webkit-animation:FadeIn 0.6s ease-out;animation:FadeIn 0.6s ease-out}.splide{margin:10px 0px;max-width:900px;width:100%}.splide .splide__track{height:100%}.splide .splide__track li{height:100%}.splide .splide__track img{border-radius:5px;object-fit:cover;width:100%;height:100%}.navItem,.navItem-focus{font-size:18px;font-family:'Inconsolata', monospace;padding:5px 20px;margin:10px 10px;background-color:transparent;color:rgba(255,250,245,0.85);border:1.75px solid rgba(255,250,245,0.85);border-radius:50px;transition:0.35s ease-in-out}.navItem:hover,.navItem-focus:hover{cursor:pointer;background:rgba(255,250,245,0.85);color:#1a1a1a;font-weight:800}.navItem-focus{border:1.75px solid #39da6e;background:#39da6e;color:#1a1a1a;font-weight:800}.navItem-focus:hover{background:#39da6e}.dot{display:inline-block;width:5px;height:5px;border-radius:50%;vertical-align:middle;margin:0px 25px}.color1{background:chartreuse}.color2{background:coral}.color3{background:#00d9ff}.color4{background:darkviolet}

